@import "./_base";
@import "./_variable";

@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Noto+Sans+TC:wght@400;700&display=swap');

* {
  padding: 0;
  margin: 0;
  font-family: 'Noto Sans TC', sans-serif; }

img {
  width: 100%;
  vertical-align: top; }

header {
  width: 100%;
  padding: 15px 30px;
  box-sizing: border-box;
  figure.logo {
    width: 145px; } }

footer {
  width: 100%;
  height: 30px;
  background-image: url(../images/bg02.png);
  background-size: 1920px; }

.num {
  font-family: 'Lato','Noto Sans TC', sans-serif; }

.ui-widget.ui-widget-content {
  border: none;
  background: #D4D1D1;
  height: 16px;
  border-radius: 8px;
  .ui-slider-range {
    background: #FFD398;
    border-radius: 8px; }
  .ui-slider-handle {
    width: 24px;
    height: 45px;
    top: -15px;
    border: none;
    margin-left: -12px;
    border-radius: 12px;
    background: #EF9B2C;
    box-shadow: 3px 3px 2px rgba(#a7a6a6,.2);
    outline: none;
    cursor: pointer; }
  @include rwd(768px) {
    height: 12px;
    border-radius: 6px;
    .ui-slider-range {
      border-radius: 6px; }
    .ui-slider-handle {
      width: 16px;
      height: 30px;
      top: -10px;
      margin-left: -8px;
      border-radius: 8px;
      box-shadow: 1px 1px 2px rgba(#a7a6a6,.2); } } }

