    section.popup {
      display: none;
      width: 100vw;
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 100;
      .mask {
        width: 100%;
        height: 100%;
        background-color: rgba(#949494,.5); }
      .container {
        max-width: 1200px;
        width: 90%;
        padding: 150px 100px;
        border-radius: 40px;
        background-color: #fff;
        box-sizing: border-box;
        @include pstc5;
        .close {
          width: 40px;
          height: 40px;
          position: absolute;
          top: 40px;
          right: 40px;
          transform: rotate(45deg);
          cursor: pointer;
          &:before,&:after {
            content: '';
            display: block;
            background-color: #ec7375;
            @include pstc0; }
          &:before {
            width: 1px;
            height: 100%; }
          &:after {
            width: 100%;
            height: 1px; } }
        .title {
          margin-bottom: 40px;
          font-size: 1.4rem;
          color: #ec7375; }
        p.txt {
          font-size: 1.1rem;
          line-height: 1.5;
          color: #5e5a5a; } } } } }
